/**
 * @fileoverview This file defines the page for editing an existing employee's profile.
 * It provides a form for HR personnel, pre-filled with the employee's details.
 */
'use client';

import * as React from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
  CardFooter,
} from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { ArrowLeft, UserPlus, Save } from 'lucide-react';
import Link from 'next/link';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { useRouter } from 'next/navigation';
import { useToast } from '@/hooks/use-toast';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { getEmployeeById } from '@/lib/data';
import { updateEmployeeAction } from './actions';
import { Skeleton } from '@/components/ui/skeleton';

const employeeSchema = z.object({
    firstName: z.string().min(1, "First name is required."),
    lastName: z.string().min(1, "Last name is required."),
    middleName: z.string().optional(),
    gender: z.string().min(1, "Gender is required."),
    dob: z.string().min(1, "Date of birth is required."),
    position: z.string().min(1, "Position is required."),
    department: z.string().min(1, "Department is required."),
    branch: z.string().min(1, "Branch is required."),
    hireDate: z.string().min(1, "Date of hire is required."),
    email: z.string().email("Invalid email address."),
});

/**
 * Renders the form to edit an employee.
 * @returns {JSX.Element} The edit employee page component.
 */
export default function EditEmployeePage({ params }) {
  const [photoPreview, setPhotoPreview] = React.useState(null);
  const [isLoading, setIsLoading] = React.useState(true);
  const router = useRouter();
  const { toast } = useToast();

  const form = useForm({
    resolver: zodResolver(employeeSchema),
    defaultValues: {
        firstName: '',
        lastName: '',
        middleName: '',
        gender: '',
        dob: '',
        position: '',
        department: '',
        branch: '',
        hireDate: '',
        email: '',
    }
  });

  React.useEffect(() => {
    const fetchEmployeeData = async () => {
        setIsLoading(true);
        const employee = await getEmployeeById(params.id);
        if (employee) {
            form.reset({
                firstName: employee.first_name,
                lastName: employee.last_name,
                middleName: employee.middle_name || '',
                gender: employee.gender,
                dob: employee.date_of_birth,
                position: employee.position,
                department: employee.department,
                branch: employee.branch,
                hireDate: employee.date_hired,
                email: employee.email,
            });
            setPhotoPreview(`https://i.pravatar.cc/150?u=${employee.email}`);
        } else {
             toast({
                variant: 'destructive',
                title: 'Error',
                description: 'Failed to fetch employee data.',
            });
        }
        setIsLoading(false);
    };
    fetchEmployeeData();
  }, [params.id, form, toast]);


  const handlePhotoChange = (event) => {
    const file = event.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setPhotoPreview(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSubmit = async (values) => {
    const result = await updateEmployeeAction(params.id, values);

    if (result.success) {
        toast({
            title: 'Success',
            description: 'Employee profile has been updated.',
        });
        router.push(`/hr/employee-data/${params.id}`);
    } else {
        toast({
            variant: 'destructive',
            title: 'Error',
            description: result.message || 'Failed to update employee profile.',
        });
    }
  };

  if (isLoading) {
    return (
        <div className="space-y-6">
             <div>
                <Button asChild variant="outline" size="sm">
                <Link href="/hr/employee-data">
                    <ArrowLeft className="mr-2" />
                    Back to Employee List
                </Link>
                </Button>
            </div>
            <Card className="max-w-4xl mx-auto">
                <CardHeader>
                    <Skeleton className="h-8 w-1/2" />
                    <Skeleton className="h-4 w-3/4" />
                </CardHeader>
                <CardContent className="space-y-6">
                    <div className="flex items-center gap-6">
                         <Skeleton className="h-24 w-24 rounded-full" />
                         <div className="space-y-2 flex-1">
                             <Skeleton className="h-6 w-1/4" />
                             <Skeleton className="h-10 w-full" />
                         </div>
                    </div>
                     <Skeleton className="h-10 w-full" />
                     <Skeleton className="h-10 w-full" />
                     <Skeleton className="h-10 w-full" />
                </CardContent>
                <CardFooter className="flex justify-end">
                    <Skeleton className="h-10 w-24" />
                </CardFooter>
            </Card>
        </div>
    )
  }

  return (
    <div className="space-y-6">
      <div>
        <Button asChild variant="outline" size="sm">
          <Link href={`/hr/employee-data/${params.id}`}>
            <ArrowLeft className="mr-2" />
            Back to Profile
          </Link>
        </Button>
      </div>

    <Form {...form}>
      <form onSubmit={form.handleSubmit(handleSubmit)}>
        <Card className="max-w-4xl mx-auto">
            <CardHeader>
            <CardTitle className="flex items-center gap-2 text-2xl font-headline">
                <UserPlus /> Edit Employee Profile
            </CardTitle>
            <CardDescription>
                Update the employee's information below.
            </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
            {/* Photo Upload Section */}
            <div className="space-y-4">
                <h3 className="text-lg font-medium">Employee Photo</h3>
                <div className="flex items-center gap-6">
                <Avatar className="h-24 w-24">
                    <AvatarImage src={photoPreview ?? undefined} alt="Employee photo preview" />
                    <AvatarFallback>
                    <UserPlus />
                    </AvatarFallback>
                </Avatar>
                <div className="space-y-2">
                    <Label htmlFor="photo-upload">Upload new photo</Label>
                    <Input id="photo-upload" type="file" accept="image/*" onChange={handlePhotoChange} />
                </div>
                </div>
            </div>
            
            {/* Personal Information Section */}
            <div className="space-y-4">
                <h3 className="text-lg font-medium">Personal Information</h3>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <FormField
                    control={form.control}
                    name="firstName"
                    render={({ field }) => (
                    <FormItem>
                        <FormLabel>First Name</FormLabel>
                        <FormControl><Input {...field} /></FormControl>
                        <FormMessage />
                    </FormItem>
                    )}
                />
                <FormField
                    control={form.control}
                    name="lastName"
                    render={({ field }) => (
                    <FormItem>
                        <FormLabel>Last Name</FormLabel>
                        <FormControl><Input {...field} /></FormControl>
                        <FormMessage />
                    </FormItem>
                    )}
                />
                <FormField
                    control={form.control}
                    name="middleName"
                    render={({ field }) => (
                    <FormItem>
                        <FormLabel>Middle Name</FormLabel>
                        <FormControl><Input {...field} /></FormControl>
                        <FormMessage />
                    </FormItem>
                    )}
                />
                </div>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <FormField
                        control={form.control}
                        name="gender"
                        render={({ field }) => (
                        <FormItem>
                            <FormLabel>Gender</FormLabel>
                            <Select onValueChange={field.onChange} value={field.value}>
                                <FormControl>
                                    <SelectTrigger><SelectValue /></SelectTrigger>
                                </FormControl>
                                <SelectContent>
                                    <SelectItem value="Male">Male</SelectItem>
                                    <SelectItem value="Female">Female</SelectItem>
                                    <SelectItem value="Other">Other</SelectItem>
                                </SelectContent>
                            </Select>
                            <FormMessage />
                        </FormItem>
                        )}
                    />
                    <FormField
                        control={form.control}
                        name="dob"
                        render={({ field }) => (
                        <FormItem>
                            <FormLabel>Date of Birth</FormLabel>
                            <FormControl><Input type="date" {...field} /></FormControl>
                            <FormMessage />
                        </FormItem>
                        )}
                    />
                </div>
            </div>

            {/* Employment Details Section */}
            <div className="space-y-4">
                <h3 className="text-lg font-medium">Employment Details</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <FormField
                    control={form.control}
                    name="position"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Position</FormLabel>
                            <FormControl><Input {...field} /></FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <FormField
                    control={form.control}
                    name="department"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Department</FormLabel>
                            <FormControl><Input {...field} /></FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <FormField
                        control={form.control}
                        name="branch"
                        render={({ field }) => (
                        <FormItem>
                            <FormLabel>Branch</FormLabel>
                            <Select onValueChange={field.onChange} value={field.value}>
                                <FormControl>
                                    <SelectTrigger><SelectValue /></SelectTrigger>
                                </FormControl>
                                <SelectContent>
                                    <SelectItem value="Headquarters">Headquarters</SelectItem>
                                    <SelectItem value="Downtown Office">Downtown Office</SelectItem>
                                    <SelectItem value="Remote">Remote</SelectItem>
                                </SelectContent>
                            </Select>
                            <FormMessage />
                        </FormItem>
                        )}
                    />
                    <FormField
                        control={form.control}
                        name="hireDate"
                        render={({ field }) => (
                        <FormItem>
                            <FormLabel>Date of Hire</FormLabel>
                            <FormControl><Input type="date" {...field} /></FormControl>
                            <FormMessage />
                        </FormItem>
                        )}
                    />
                </div>
            </div>

            {/* Contact Information Section */}
            <div className="space-y-4">
                <h3 className="text-lg font-medium">Contact Information</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <FormField
                    control={form.control}
                    name="email"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Email Address</FormLabel>
                            <FormControl><Input type="email" {...field} /></FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                </div>
            </div>
            </CardContent>
            <CardFooter className="flex justify-end">
                 <Button type="submit" disabled={form.formState.isSubmitting}>
                    <Save className="mr-2"/>
                    Save Changes
                </Button>
            </CardFooter>
        </Card>
      </form>
    </Form>
    </div>
  );
}
